html{
  color:$staycity_black;
}

body{
  background:$staycity_yellow;

  header{
    width:80%;
    margin:2em auto;
    background:$transparent_white;
    padding:1em;
    border-radius:4px;
    box-shadow:$boxshadow;
  }
}

.logo{
  width:300px;
  height:200px;
  position:absolute;
  bottom:0;
  right:0;

  img{
    width:100%;
    height:auto;
    display:block;
  }
}